div.right-col {
	min-height: 0 !important;
}

small.info-field {
	font-size: 13px;
	font-family: "Helvetica Neue", Roboto, Arial, "Droid Sans", sans-serif;
	line-height: 20px;
}

.form-control-feedback-input {
	margin-top: 25px;
}

div.thumbnail.selected {
	p {
		color: white;
		font-weight: bold;
		line-height: 25px;
	}
}

.form-control.has-feedback-left {
	padding-left:40px;
}

.image_picker_image {
    max-height: 150px;
}

.form-control-feedback {
	z-index: 1;
}

.stepContainer {
	height: auto !important;
	overflow-x: visible !important;
}

.manage-rows-form {
	margin-top: 12px;
}

.rm-field {
	display: none;
}

.form-group {
    min-height: 80px;
}

.item .alert {
    display: block;
    float: none;
    margin: 0;
    border-radius: 0;
    padding: 0 3px;
    width: auto;

    &::after {
    	content: none;
    }

}
